import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'

import { BootstrapVue} from 'bootstrap-vue'

// Import Bootstrap and BootstrapVue CSS files (order is important)
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

// Make BootstrapVue available throughout your project
Vue.use(BootstrapVue)


import ProjectsCard from './components/ProjectsCard.vue'
import PortifolioCard from './components/PortifolioCard.vue'
import ResumeCard from './components/ResumeCard.vue'
import EducationCard from './components/EducationCard.vue'
import ExperiencesCard from './components/ExperiencesCard.vue'
import ContactForm from './components/ContactForm.vue'



Vue.use(VueRouter)

const router = new VueRouter({
  routes:[
  {
    path:'/',
    component: PortifolioCard
  },

  {
    path:'/myprojects',
    component: ProjectsCard
  },
  {
    path:'/myresume',
    component: ResumeCard
  },
  {
    path:'/myeducation',
    component: EducationCard
  },
  {
    path:'/myexperiences',
    component: ExperiencesCard
  },
  {
    path:'/mycontact',
    component: ContactForm
  },
  ],
  scrollBehavior(){
    document.getElementById("app").scrollIntoView();
  }

  /*scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition; // Retorna para a posiÃ§Ã£o anterior ao voltar
    } else {
      return { x: 0, y: 0 }; // Rola para o topo ao mudar de rota
    }
  }*/

})

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')