<script>

export default{
    name: "contact-form",
    data(){
        return{
        errors: [],
        List:[],  
        itemList:{
        nome:"", 
        email:"", 
        assunto:"", 
        mensagem:""},
        tasksContacts:[],
      }   
    },

    created(){
      this.tasksContacts  = (localStorage.getItem("tasks")) ? JSON.parse(localStorage.getItem("tasks")): [];
    },

    methods: {
      checkForm: function(e){
      this.errors= [];
    
      if(!this.itemList.nome){
        this.errors.push("O nome Ã© obrigatÃ³rio");
      }  

      if(!this.itemList.email){
        this.errors.push("O e-mail Ã© obrigatÃ³rio");     

      }else if(!this.validEmail(this.itemList.email)){
        this.errors.push("Utilize um e-mail vÃ¡lido!");
      }

      if(!this.errors.length){
        this.saveTask()
        return true;
      }
      
      e.preventDefault();

    },

    validEmail: function(email){
      var re =  /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);

    },

      addContacts(itemList){
            itemList.id = Date.now();
            this.List.push(itemList);
            this.itemList = {checked: false}; 
      },

      saveTask(){
            let tasks = (localStorage.getItem("tasks")) ? JSON.parse(localStorage.getItem("tasks")): [];
            tasks.push(this.itemList);
            localStorage.setItem("tasks", JSON.stringify(tasks));
            this.routerLink.push({name: "portifolio-card"})
      },
     
  }
}

